.listItem{
    width: 250px;
    height: 180px;
    background-color:#C0DBEA;
    margin-right: 5px;
    overflow: hidden;
    cursor: pointer;
    color: rgb(60, 10, 227);

    img{
        width: 100%;
        height: 120px;
        object-fit: cover;
    }
    nav{
        color: black;
    }
    .listitemdetails{
        color:teal;
        margin: 10px;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &:hover{
        width: 330px;
        height: 300px;
        -webkit-box-shadow :0px 2px 15px 0px rgba(255, 255, 255, 0.948);
        box-shadow: 0px 2px 15px 0px rgba(255,255,255,0.07);
        position: absolute;
        top:-100px;
        border-radius: 5px;

        img
        {
            height: 140px;
        }

        .itemInfo
        {
            display: flex;
            flex-direction: column;
            padding: 5px;

            .icons
            {
                display: flex;
                margin-bottom: 10px;

                .icon{
                   border: 2px solid rgb(213, 9, 118);
                   padding: 5px;
                   border-radius: 50%;
                   margin-right: 10px;
                   font-size: 16px;
                }
            }

            .itemInfoTop
            {
                display: flex;
                align-items: center;
                margin-bottom: 2px;
                font-size: 14px;
                font-weight: 600;
                color: gray;

                .limit
                {
                    border: 1px solid gray;
                    padding: 1px 3px;
                    margin: 0px 10px;
                }
            }

        }
    }
}

.speaker{
    color:lightcoral;
    padding-left:  10px;
}
.type
{
    color: #000;
    font-weight: bold;
    margin-left: 20px;
}
